* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

#root {
  height: max-content;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #23252c;
  padding: 2.6rem 0;
}

.container {
  width: 317px;
  background: #1a1b21;
  border-radius: 0px 0px 10px 10px;
}
header > div {
  display: flex;
  flex-direction: column;
  align-items: center;
}
header > div h1 {
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #ffffff;
  margin-top: 21px;
}

.work {
  font-weight: 400;
  font-size: 12.8px;
  line-height: 15px;
  text-align: center;
  color: #f3bf99;
}
.weblink {
  font-weight: 400;
  font-size: 10.24px;
  line-height: 150%;
  text-align: center;
  color: #f5f5f5;
  margin-top: 5px;
}
.buttons {
  display: flex;
  width: 100%;
  justify-content: center;
  margin-top: 0.5rem;
}
.buttons button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;
  gap: 8px;

  border-radius: 6px;
  width: 115px;
  height: 34px;
  margin: 0 3px;
}
.buttons button img {
  width: 16px;
  height: 16px;
}
.buttons button p {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
}
.emailButton {
  border: 1px solid #d1d5db;
  background: #ffffff;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  color: #374151;
}
.linkendinButton {
  background: #5093e2;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  color: #ffffff;
  border: 1px solid #387ece;
}
main {
  margin: 24px 36px;
}
main section h2 {
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  color: #f5f5f5;
}
main section p {
  font-weight: 400;
  font-size: 10.24px;
  line-height: 150%;
  color: #dcdcdc;
}
footer {
  display: flex;
  align-items: center;
  padding: 20px 72px;
  justify-content: space-between;
  width: 317px;
  height: 64px;
  background: #161619;
  border-radius: 0px 0px 10px 10px;
}
footer img {
  width: 25px;
  height: 25px;
}
